<template>
    <main class="app-main">
        <section class="top">
            <p class="title"><md-icon class="go-back" name="arrow-left" size="lg" @click="goBack"></md-icon>意见反馈</p>
        </section>
        <div class="con-con">
            <div class="title">
                <div class="left">标题</div>
                <input type="text" class="text" v-model="feedTitle"/>
            </div>
            <div class="detail">
                <div class="left">内容</div><textarea v-model="feedDetail" class="profileText" cols="2" rows="6" style="overflow:hidden"></textarea>
            </div>

        </div>
        <md-button type="default" @click="sendFeed">提交</md-button>
    </main>
</template>

<script>
import { Field, CellItem, Toast, Icon, InputItem, Button } from 'mand-mobile'
import { sendFeed } from '../../../apiUrl/personal/server'

export default {
  name: 'index',
  components: {
    [Field.name]: Field,
    [CellItem.name]: CellItem,
    [Icon.name]: Icon,
    [Button.name]: Button,
    [InputItem.name]: InputItem
  },
  data () {
    return {
      feedTitle: '',
      feedDetail: ''
    }
  },
  methods: {
    // 回退
    goBack () {
      this.$router.push('/personal')
    },
    sendFeed () {
      sendFeed({ detail: this.feedDetail, title: this.feedTitle }).then((res) => {
        if (res.status === 200) {
          Toast.succeed('反馈成功')
          this.$router.push('/personal')
        }
      })
    }
  }
}
</script>

<style lang="stylus" scoped>
    .app-main
        overflow hidden
    .top
        .title
            background url("../../../assets/personal/bg.png") no-repeat
    .con-con
        width 90%
        overflow hidden
        margin 0px auto
        margin-top 118px
        margin-bottom 60px
        .title
            width 100%
            margin-bottom 38px
            overflow hidden
            .left
                font-size 28px
                line-height 66px
                float left
                margin-right 20px
            .text
                border 2px solid rgba(153,153,153,.4);
                outline 0!important
                float left
                height 66px
                width 586px
                color #999
                font-size 32px
                border-radius 4px
                text-indent 24px
                background #ffffff
                box-shadow 0px 4px 24px 0px rgba(2,25,85,0.1)
                border-radius 4px
        .detail
            width 100%
            overflow hidden
            float left
            .left
                font-size 28px
                line-height 66px
                float left
                margin-right 4px
            .profileText
                width 586px
                height 500px
                color #999
                padding  24px
                font-size 32px
                border 2px solid rgba(153,153,153,.4)
                background #ffffff
                box-shadow 0px 4px 24px 0px rgba(2,25,85,0.1);
                border-radius 4px;
                outline 0!important
    /deep/.md-button
        width:580px;
        height:72px;
        font-size 28px
        background:rgba(2,25,85,1);
        border-radius:8px;
        margin 0 auto
        margin-top 60px
        color #fff
    .md-button.default:after
        border none
</style>
